body,
button,
input,
select,
textarea {
  font-family: "PTSans-Regular";
  color: #496e86;
  font-size: 16px; }

.display-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.logo-img {
  width: 359px;
  height: 84px; }

.meta {
  display: flex;
  justify-content: space-around;
  padding: 14px 0;
  font-family: "FontAwesome";
  color: #7bbfec;
  font-size: 14px; }
  .meta.meta-white {
    justify-content: flex-start; }
    .meta.meta-white .messag:before {
      margin-left: 20px; }
  .meta-span {
    content: "";
    display: inline-block;
    font-family: "FontAwesome";
    color: #7bbfec;
    font-size: 14px; }
    .meta-span.white {
      color: #ffffff; }
  .meta .views:before {
    content: "\f06e";
    padding-right: 5px; }
  .meta .messag:before {
    content: "\f27a";
    padding-right: 5px; }

.soclink {
  font-family: "FontAwesome";
  color: #ffffff;
  font-size: 16px;
  padding: 7px; }
  .soclink:hover {
    color: #31cf87;
    background-color: #ffffff;
    border-radius: 5px; }

.title-articl {
  width: 100%;
  font-family: "PTSerif-Bold";
  color: #496e86;
  font-size: 20px;
  padding: 0 0 20px 0; }
  .title-articl:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #ffffff;
    margin-top: 20px; }
  .title-articl.right {
    display: inline-block;
    padding: 20px 0 0 70px; }
  .title-articl:after {
    border-color: #7bbfec; }

.title-main {
  font-family: font-serifbold;
  color: #7bbfec;
  font-size: 24px;
  text-align: center;
  position: relative;
  font-weight: 700; }
  .title-main:after {
    content: "";
    display: block;
    width: 200px;
    height: 3px;
    border-radius: 50%;
    background: rgba(29, 82, 115, 0.3);
    background-image: -webkit-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -moz-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -ms-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -o-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    margin: 10px auto 0 auto; }

.items-title {
  font-family: "PTSans-Regular";
  color: #00d381;
  font-size: 14px; }
  .items-title.white {
    color: #ffffff; }
    .items-title.white:after {
      margin: 20px 0; }
  .items-title:after {
    content: "";
    display: block;
    width: 20px;
    height: 1px;
    background-color: #00d381;
    margin: 7px auto; }

.items-content {
  text-align: center;
  padding: 20px; }
  .items-content .items-title-articl {
    font-family: "PTSans-Regular";
    color: #496e86;
    font-size: 14px; }

.items {
  flex-basis: calc(100%/4 - 20px); }

.row-heading {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 1px solid #f1f9fd;
  background-color: #f1f9fd;
  padding: 40px; }

.heading-item {
  height: 155px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: relative;
  z-index: 1;
  background-image: url(/images/background/pop1.jpeg);
  background-position: center;
  background-size: cover;
  font-family: "PTSerif-Regular";
  color: #1d5273;
  font-size: 16px;
  margin: 0 auto;
  padding: 0 18px;
  text-align: center;
  box-sizing: border-box;
  transition: .5s; }
  .heading-item:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #ffffff;
    margin-top: 20px; }
  .heading-item:after {
    border-color: #1d5273;
    margin-top: 15px; }
  .heading-item:hover {
    background-color: #00d381;
    background-image: none;
    color: #00d381; }
    .heading-item:hover:after {
      border-color: #00d381; }
  .heading-item:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 50%;
    margin: 10px;
    z-index: -1; }

.publication-content {
  padding-left: 20px; }

.pub-picture {
  width: 70px;
  height: 70px; }

.link-piblicat {
  display: block;
  color: #496e86;
  padding: 5px 5px 15px 5px; }
  .link-piblicat:hover {
    color: #77c1ee; }

.pub-link {
  color: #7cc0ed; }
  .pub-link:hover {
    color: #2aa1f0; }

